<template>
    <div class="header">我的订单</div>
    <div class="container">
        <!-- 循环展示各个商店的订单信息 -->
        <div class="container__item" v-for="item in orderList" :key="item.shopId">
            <div class="container__item__header">
                <div class="container__item__header__left">{{item.shopName}}{{conM(item)}}</div>
                <div class="container__item__header__right">已取消</div>
            </div>
            <div class="container__item__body">
                <div class="container__item__body__left">
                    <div class="container__item__body__left__item" v-for="productItem in item.itemList" :key="productItem.id">
                        <img :src="productItem.imgUrl" alt="" class="container__item__body__left__item__img">{{conM(productItem.imgUrl)}}
                    </div>
                </div>
                <div class="container__item__body__right">
                    <div class="__sum-price">￥{{sumPrice}}</div>
                    <div class="__sum-count">共{{sumCount}}件</div>
                </div>
            </div>
        </div>
    </div>
    <Docker tag='order'></Docker>
</template>
<script>
import { ref } from '@vue/reactivity'
import { get } from '../../utils/request'
import { computed } from '@vue/runtime-core'
import Docker from '../../components/Docker'
const getOrderListEffect = () => {
  const orderList = ref([])
  const getOrderList = async () => {
    const request = await get('/api/order-list/1')
    if (request.data.code === '0000') {
      orderList.value = request.data.data
    } else {
      orderList.value = []
    }
  }
  const sumPrice = computed((itemList) => {
    let result = 0
    for (const key in itemList) {
      result += itemList[key].count
    }
    return result.toFixed(2)
  })
  const sumCount = computed((itemList) => {
    let result = 0
    for (const key in itemList) {
      result += itemList[key].count * itemList[key].promotionPrice
    }
    return result.toFixed()
  })
  getOrderList()
  console.log(orderList)
  return { orderList, sumPrice, sumCount }
}
export default {
  name: 'OrderList',
  components: { Docker },
  setup () {
    const { orderList, sumPrice, sumCount } = getOrderListEffect()
    const conM = (item) => {
      console.log(item)
    }
    return { orderList, sumPrice, sumCount, conM }
  }
}
</script>
<style lang="scss" scoped>
.header{
    height: 44rem;
    background-color: #ffffff;
    line-height: 44rem;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
}
.container{
    position: fixed;
    top: 44rem;
    left: 0;
    right: 0;
    bottom: 49rem;
    background-color: #f8f8f8;
    padding:16rem 18rem;
    &__item{
        box-sizing: border-box;
        height: 110rem;
        width: 339rem;
        padding:16rem;
        border-radius: 4px;
        background: #FFFFFF;
        margin-bottom: 16rem;
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 16rem;
            &__left{
                font-size: 16px;
                color: #333333;
                font-family: PingFangSC-Medium;
                height: 22rem;
                line-height: 22rem;
                text-align: center;
            }
            &__right{
                font-size: 14px;
                color: #999999;
                text-align: center;
                height: 20rem;
                line-height: 20rem;
                font-family: PingFangSC-Medium;
            }
        }
        &__body{
            display: flex;
            align-items: center;
            justify-content: space-between;
            &__left{
                display: flex;
                align-items: center;
                &__item{
                    margin-right: 12rem;
                    &__img{
                        width: 40rem;
                        height: 40rem;
                    }
                }
            }
            &__right{
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                &__sum-price{
                    font-size: 14px;
                    color: #E93B3B;
                    text-align: right;
                    font-family: PingFangSC-Regular;
                    margin-bottom: 4rem;
                }
                &__sum-count{
                    font-size: 12px;
                    color: #333333;
                    text-align: right;
                    line-height: 14px;
                    font-family: PingFangSC-Regular;
                }
            }
        }
    }
}
</style>
